  const YaafMinerRewards = useContractReader(readContracts, "YaafMiner", "YaafMinerRewards", [address,], localProviderPollingTime);


  const referralAllRebate = useContractReader(readContracts, "YaafMiner", "getReferralAllRebate", [address], localProviderPollingTime);
  const getClaimYaafMiner = useContractReader(readContracts, "YaafMiner", "getClaimYaafMiner", [address], localProviderPollingTime);
  const calculateYaafMinerMerge = useContractReader(readContracts, "YaafMiner", "calculateYaafMinerMerge", [getClaimYaafMiner ? getClaimYaafMiner : 0,], localProviderPollingTime);
  const referralAllInvitee = useContractReader(readContracts, "YaafMiner", "getReferralAllInvitee", [address], localProviderPollingTime);
  const yourLocalBalance = useContractReader(readContracts, "BUSD", "balanceOf", [address], localProviderPollingTime);
  const contractsYaaf=readContracts.YaafMiner
  
  const contractsAddress = "0xC03fc5a1473060C887b82131Db66B7dD1f34c522"
  const approvedBalance = useContractReader(readContracts, "BUSD", "allowance", [address, contractsAddress], localProviderPollingTime);
